.socialmedia a, .socialmedia span {
  /* color: #044aa3;     */
  cursor: pointer;
  z-index: 999; }

.modal-content a {
  cursor: pointer; }

.socialmedia a:hover {
  text-decoration: none;
  color: #044AA3; }

.fa-code:hover:before {
  opacity: 0.5; }

.fa-twitter:hover:before {
  opacity: 0.5; }

.fa-facebook:hover:before {
  opacity: 0.5; }

.fa-table:hover:before {
  opacity: 0.5; }

.fa-info:hover:before {
  opacity: 0.5; }

.fa-caret-down:hover:before {
  opacity: 0.5; }

.fa-check-square-o:hover:before {
  opacity: 0.5; }

.fa-square-o:hover:before {
  opacity: 0.5; }

.fa-times {
  color: #000; }

.fa-times:hover:before {
  opacity: 0.5; }

/* Default
 * No @media query for XS is necessary
 */
.socialmedia {
  font-size: .9rem;
  color: #044AA3 !important; }

.socialmedia a {
  color: #044AA3; }

.socialmedia a:hover {
  text-decoration: none;
  color: #044AA3; }

.modal-body {
  font-size: .875em; }

.modal-header {
  font-size: .875em;
  font-weight: 700; }

#logo img {
  height: 70px;
  width: 213px;
  position: fixed;
  right: -9px;
  top: -12px;
  display: inline-block; }

.logo {
  position: absolute;
  right: -9px;
  top: -2px;
  display: inline-block;
  flex-direction: row; }

.logo #estatLogo {
  width: 12rem;
  height: auto;
  margin-left: -4px; }

#nsiConteiner {
  position: relative;
  xborder-bottom: 2px solid #f51c40;
  display: inline-block; }

#nsiConteiner:after {
  content: "";
  position: absolute;
  right: 0;
  z-index: 100;
  top: 20%;
  width: 1px;
  height: 60%;
  background: #1e487d; }

#nsiConteiner img {
  margin-right: 5px; }

/* Large. Above 62em (992px) */
@media screen and (min-width: 62em) {
  .socialmedia {
    font-size: 1em; } }

/* Extra large. Above 75em (1200px) */
@media screen and (min-width: 75em) {
  .socialmedia {
    font-size: 1em; } }

#logo-desktop {
  cursor: default; }
